<template>
  <div class="main_wrapper">
    <div
      class="py-5 bg-light c2a1 wrapper"
      style="background:linear-gradient(rgba(0,0,0,.2), rgba(0,0,0, .2)), url('/img/showcase/tour-1-1.jpg')"
    >
      <div class="container">
        <!-- Row -->
        <div class="row justify-content-center">
          <div class="col-md-7 text-center">
            <h2 class="mb-3 text-white font-weight-medium">
              WHAT ARE YOU WAITING FOR?
            </h2>
            <p class="font-weight-light text-white op-8">
              {{ getOneTour.duration }} days. 1 adventure. Infinite memories.
              Make it yours today!
            </p>
            <button
              v-if="auth"
              :disabled="paymentLoading"
              @click="bookings(getOneTour._id)"
              class="btn btn-danger-gradiant btn-md border-0 text-white mt-3 text-uppercase"
            >
              Book Tour Now!
            </button>

            <router-link
              v-if="!auth"
              to="/login"
              class="btn btn-danger-gradiant btn-md border-0 text-white mt-3 text-uppercase"
            >
              Log In to Book Your Tour
            </router-link>
          </div>
        </div>
        <!-- Row -->
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';

export default {
  computed: mapGetters(['getOneTour', 'paymentLoading', 'getSession']),
  methods: {
    ...mapActions(['checkout']),
    bookings(tourId) {
      const stripe = window.Stripe(
        'pk_test_dJzzxtLsf6alYgHLYuXU7YWf00xe1OnWxt'
      );
      // get session from api
      this.checkout(tourId).then(res => {
        if (res && res.data.success) {
          //create checkout form-charge

          stripe.redirectToCheckout({
            sessionId: this.getSession.id
          });
        }
      });
    }
  }
};
</script>

